// R E A C T
import React from 'react';
import ReactDOM from 'react-dom';

// C O M P O N E N T S
import Dash from './components/Dash';
import GroupName from './components/GroupName';
import Intro from './components/Intro';
import TechStack from './components/TechStack';
import TopNavigation from './components/TopNavigation';
import GetInTouch from './components/GetInTouch';
import ProjectLirikLive from './components/workshowcase/ProjectLirikLive'
import Artworks from './components/workshowcase/Artworks';

import './scrolling';

function App() {

    const galleryURLS = ["https://images.unsplash.com/photo-1572635196237-14b3f281503f"];

    return (
        <React.Fragment>
            <div className="loading-screen">
                <img src="https://i.giphy.com/media/17mNCcKU1mJlrbXodo/giphy.webp" alt=""/>
            </div>
            <TopNavigation/>
            <Intro/>
            <Dash/>
            <TechStack/>
            <GroupName counter={1} title="PROJECTS"/>
            <ProjectLirikLive/> {/* Project Components Go Here  */}

            <div className="more-will-come">
                <div className="container">
                    <span className="h-center">
                        More projects will be listed soon.<br/>
                        <span
                            style={{
                            fontSize: "18px"
                        }}>I want to advance my skills before working on larger projects.</span>
                    </span>
                </div>
            </div>

            <GroupName counter={2} title="GALLERY"/>
            <Artworks/> {/* <Footer /> */}
            <GroupName counter={3} title="CONTACT"/>
            <GetInTouch/>

            <div className="container">
                <div className="eof">
                    <span>All rights reserved ©2021</span>
                    <Dash/>
                    <span>Thanks for visiting</span>
                </div>
            </div>

        </React.Fragment>
    )
}

ReactDOM.render(
    <App/>, document.querySelector("#app"));